.te-dialog-product.te-tablet {
  display: flex;
}
.te-dialog-product.te-desktop {
  justify-content: flex-end;
  margin: 0 auto;
  flex-direction: row;
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: flex;
}
.te-dialog-product-container {
  max-width: 1350px;
  width: 100%;
  height: 100%;
  position: relative;
  margin: 0 auto;
}
.te-dialog-product-image {
  box-sizing: border-box;
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  display: -webkit-box;
  display: flex;
  flex-direction: column-reverse;
  margin: 0 auto;
  align-items: center;
  background-color: white;
  justify-content: center;
  width: 100%;
  animation-duration: 1s;
  animation-fill-mode: both;
}
.te-dialog-product-image.te-tablet {
  align-self: start;
  width: 50%;
}
.te-dialog-product-image.te-desktop {
  width: 60%;
  flex-basis: 60%;
  padding-left: 20px;
  flex-direction: row;
  height: 100%;
  max-height: unset;
  align-items: unset;
}
.te-dialog-product-thumbnails {
  display: block;
  position: relative;
  overflow: visible;
  width: 100%;
}
.te-dialog-product-thumbnails.te-mobile {
  margin: 0;
  height: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.te-dialog-product-thumbnails.te-desktop {
  height: 100px;
  width: 100px;
  margin: 0;
}
.te-dialog-product-slider-dots {
  z-index: 999;
  position: absolute;
  width: 120px;
  overflow: hidden;
  height: 20px;
  bottom: 45px;
  left: 50%;
  transform: translateX(-50%);
}
.te-dialog-product-slider-dots-container {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  transition: transform 0.5s ease;
}
.te-dialog-product-slider-dots-single {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.te-dialog-product-slider-dots-single-dot {
  box-shadow: 1px 1px 2px 0.3px rgba(0, 0, 0, 0.77);
  border: none;
  font-size: 0;
  background-color: white;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  padding: 0;
}
.te-dialog-product-slider-dots-single-dot.transition {
  transition: all 0.1s linear;
}
.te-dialog-product-slider-dots-single-dot.size0 {
  width: 13px;
  height: 13px;
  opacity: 1;
}
.te-dialog-product-slider-dots-single-dot.size1 {
  width: 11px;
  height: 11px;
  opacity: 0.5;
}
.te-dialog-product-slider-dots-single-dot.size2 {
  width: 9px;
  height: 9px;
  opacity: 0.5;
}
.te-dialog-product-slider-dots-single-dot.size3 {
  width: 7px;
  height: 7px;
  opacity: 0.5;
}
.te-dialog-product-slider-dots-single-dot.size4 {
  width: 4px;
  height: 4px;
  opacity: 0.5;
}
.te-dialog-product-slider-dots-single-dot.size5 {
  width: 2px;
  height: 2px;
  opacity: 0.5;
}
.te-dialog-product-slider {
  width: 100%;
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.te-dialog-product-slider .slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.te-dialog-product-slider .slick-list .slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 600px;
}
.te-dialog-product-slider .slick-list .slick-track .slick-slide {
  display: block;
  text-align: center;
}
.te-dialog-product-slider .slick-list .slick-track .slick-slide,
.te-dialog-product-slider .slick-list .slick-track .slick-slide * {
  outline: none !important;
}
.te-dialog-product-slider.te-desktop {
  width: 100px;
}
.te-dialog-product-slider.te-mobile {
  height: 100%;
  display: flex;
  align-items: center;
}
.te-dialog-product-slider.te-mobile .slick-list {
  max-height: 100%;
  width: 100%;
}
.te-dialog-product-slider.te-mobile .slick-track {
  display: flex;
  align-items: center;
}
.te-dialog-product-slider-iiz.te-mobile {
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  cursor: zoom-in;
  display: flex;
  align-items: center;
  width: 100%;
}
.te-dialog-product-slider-iiz.te-mobile img:first-child,
.te-dialog-product-slider-iiz.te-tablet img:first-child,
.te-dialog-product-slider-iiz.te-desktop img:first-child {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  display: block;
  pointer-events: none;
  margin: 0 auto;
  object-fit: contain;
}
.te-dialog-product-slider-iiz.te-tablet {
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  cursor: zoom-in;
  display: flex;
  align-items: center;
  width: 100%;
}
.te-dialog-product-slider-iiz.te-desktop {
  display: flex;
  align-items: center;
  max-height: 700px;
  height: unset;
  margin: 0 25px;
}
.te-dialog-product-thumb .te-img::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.03);
  z-index: 999999;
}
.te-dialog-product-thumb.te-desktop .te-img {
  height: 100px;
  width: 100px;
  background-size: cover;
}
.te-dialog-product-thumb.te-tablet .te-img {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
  min-height: 80px;
  width: 100px;
  background-size: cover;
}
.te-dialog-product-thumb.active {
  opacity: 0.5;
}
.te-dialog-product-arrow.te-desktop.slick-next,
.te-dialog-product-arrow.te-desktop.slick-prev {
  transform: translateX(-50%) rotate(90deg);
  opacity: 1;
  height: 50px;
  width: 50px;
  left: 50%;
}
.te-dialog-product-arrow.te-tablet.slick-next,
.te-dialog-product-arrow.te-tablet.slick-prev {
  opacity: 1;
}
.te-dialog-product-arrow.te-desktop.slick-next {
  top: 90%;
}
.te-dialog-product-arrow.te-desktop.slick-prev {
  top: 0;
}
.te-dialog-product-details {
  word-break: break-word;
}
.te-dialog-product-details.te-mobile {
  padding: 0 20px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.te-dialog-product-details.te-tablet {
  width: 50%;
  padding-left: 15px;
}
.te-dialog-product-details.te-desktop {
  width: calc(40% - 50px);
  flex-basis: calc(40% - 50px);
  margin-right: 50px;
  margin-left: 10px;
}
.te-dialog-product-details-basic {
  font-weight: 500;
  text-align: left;
  display: -webkit-box;
  display: flex;
  flex-direction: column;
}
.te-dialog-product-details-basic.te-mobile {
  display: grid;
  grid-template-areas: 'productBrand' 'productName' 'productPrice';
  margin-bottom: 20px;
}
.te-dialog-product-details-basic-name {
  font-size: 1.25em;
  line-height: 25px;
  font-weight: 500;
  text-transform: uppercase;
  color: #4f5058;
  order: 2;
}
.te-dialog-product-details-basic-name.te-mobile {
  padding-right: 20px;
  grid-area: productName;
  font-size: 1em;
}
.te-dialog-product-details-basic-brand {
  margin-bottom: 3px;
  font-size: 1em;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2px;
  color: #a3a0a0;
  order: 1;
}
.te-dialog-product-details-basic-brand.te-mobile {
  grid-area: productBrand;
  align-self: start;
  font-size: 0.875em;
  padding-right: 20px;
}
.te-dialog-product-details-basic-price {
  margin-top: 4px;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  order: 3;
}
.te-dialog-product-details-basic-price.te-mobile {
  grid-area: productPrice;
  margin-top: 0;
}
.te-dialog-product-details-basic-price.te-mobile,
.te-dialog-product-details-basic-price.te-tablet,
.te-dialog-product-details-basic-price.te-desktop {
  display: grid;
  grid-template-areas: 'priceMain vatInfo' 'discPercentage discPercentage';
}
.te-dialog-product-details-basic-price-main {
  grid-area: priceMain;
  font-weight: 400;
  font-size: 1.25em;
  display: flex;
  align-items: center;
}
.te-dialog-product-details-basic-price-main.te-mobile {
  font-size: 0.875em;
}
.te-dialog-product-details-basic-price-main-sales {
  color: #ec877b;
  margin-left: 10px;
  letter-spacing: 0;
  order: 2;
}
.te-dialog-product-details-basic-price-main-sales.te-mobile {
  margin-left: 0;
}
.te-dialog-product-details-basic-price-main-regular {
  color: #4f5058;
  text-decoration: line-through;
}
.te-dialog-product-details-basic-price-main-vat {
  grid-area: vatInfo;
  color: #a3a0a0;
  font-size: 0.75em;
  font-weight: 400;
  margin-top: 0.3em;
  margin-left: 5px;
  text-indent: 2px;
}
.te-dialog-product-details-basic-price-main-vat.te-mobile {
  font-size: 0.6875em;
}
.te-dialog-product-details-basic-price-main-discount {
  margin-top: 5px;
  font-size: 1.25em;
  grid-area: discPercentage;
  color: #ec877b;
  font-weight: 400;
}
.te-dialog-product-details-basic-price-main-discount.te-mobile {
  font-size: 0.875em;
}
.te-dialog-product-details-basic-price-main-units {
  color: #a3a0a0;
  font-size: 0.75em;
  margin-top: 5px;
  order: 4;
}
.te-dialog-product-warning-message {
  color: #ec877b;
  padding-bottom: 10px;
}
.te-dialog-product-buy_button {
  cursor: pointer;
  margin: 0;
  width: 100%;
  font-size: 0.8em;
}
.te-dialog-product-buy_button.disabled {
  cursor: not-allowed;
}
.te-dialog-product-buy_button.disabled .te-button-body {
  background-color: #dddddd;
}
.te-dialog-product-buy_button.disabled .te-button-body .te-button-text {
  color: #525860;
}
.te-dialog-product-description-link {
  padding-bottom: 17px;
}
.te-dialog-product-description-link:hover {
  cursor: pointer;
}
.te-dialog-product-description-link-title {
  font-weight: 400;
  color: #303248;
  text-transform: capitalize;
  text-align: left;
  display: block;
  font-size: 12px;
  padding: 10px 10px 10px 0;
  position: relative;
  text-decoration: underline;
  margin-top: 20px;
}
@keyframes fadeIn {
  0% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
@keyframes loadingAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadeIn {
  animation-name: fadeIn;
}
