.te-dialog-color-relations-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 450px;
  height: 120px;
  margin: 20px 0;
}
.te-dialog-color-relations-title {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  color: #303248;
}
.te-dialog-color-relations-tile {
  margin: 4px;
}
.te-dialog-color-relations-image {
  max-width: 100%;
  max-height: 95px;
  display: block;
}
.te-dialog-color-relations-image:hover {
  cursor: pointer;
}
.te-dialog-color-relations-image.active {
  border-bottom: 3px solid #4f5058;
}
.te-dialog-color-relations-products .slick-track {
  margin-left: 0;
}
.te-dialog-color-relations-arrow {
  z-index: 1;
  position: absolute;
  display: block;
  height: 50px;
  width: 50px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: #000;
  padding: 0;
  border: none;
  outline: none;
  opacity: 1;
}
.te-dialog-color-relations-arrow.slick-next {
  left: 100%;
  top: 50%;
  transform: translate(-100%, -50%);
}
.te-dialog-color-relations-arrow.slick-prev {
  left: 6px;
  top: 50%;
  transform: translate(-50%, -50%);
}
