.te-dialog-similar-container {
  color: #444444;
}
.te-dialog-similar-title {
  font-weight: 500;
  font-size: 1em;
  margin: 15px 10px;
  color: #4f5058;
}
.te-dialog-similar-products-container {
  margin: 10px 10px 25px 10px;
  font-weight: 500;
  font-size: 0.75em;
}
.te-dialog-similar-products-container:hover {
  cursor: pointer;
}
.te-dialog-similar-products-image-container {
  display: flex;
  align-items: center;
  height: 270px;
  position: relative;
}
.te-dialog-similar-products-image-container::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.03);
  z-index: 999999;
}
.te-dialog-similar-products-image {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: block;
}
.te-dialog-similar-products-image.cover {
  object-fit: cover;
}
.te-dialog-similar-products-image.contain {
  object-fit: contain;
}
.te-dialog-similar-products-brand {
  color: #a3a0a0;
  margin: 10px 0 0 0;
  line-height: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.te-dialog-similar-products-brand.te-desktop {
  margin-bottom: 2px;
}
.te-dialog-similar-products-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #4f5058;
  line-height: 16px;
}
.te-dialog-similar-products-name.te-desktop {
  margin-bottom: 2px;
}
.te-dialog-similar-products-price-container {
  display: flex;
  align-items: center;
}
.te-dialog-similar-products-price-sales {
  color: #ec877b;
  margin-left: 10px;
}
.te-dialog-similar-products-price-regular {
  text-decoration: line-through;
  color: #4f5058;
}
.te-dialog-similar-products-no-sales {
  color: #4f5058;
}
.te-dialog-similar-arrow {
  top: 40%;
  opacity: 1;
  z-index: 1;
  position: absolute;
  display: block;
  height: 50px;
  width: 50px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: #000;
  padding: 0;
  border: none;
  outline: none;
}
.te-dialog-similar-arrow.slick-next {
  left: calc(100% - 5px);
  transform: translate(-100%, -50%);
}
.te-dialog-similar-arrow.slick-prev {
  left: 2px;
  transform: translate(0, -50%);
}
