.te-checkout {
  background-color: #f5f5f5;
  box-sizing: border-box;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 10px;
  position: relative;
  width: 100%;
}
.te-checkout-columns-layout {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
}
.te-checkout-columns-layout.mobile {
  flex-direction: column;
}
.te-checkout-columns-layout.mobile .te-checkout-column,
.te-checkout-columns-layout.mobile .te-checkout-column-small {
  max-width: 100%;
  padding: 0;
  width: 100%;
}
.te-checkout-columns-layout.mobile .te-checkout-heading.desktop.toggle {
  padding-left: 15px;
  margin: 0;
}
.te-checkout-column {
  padding-left: 35px;
  width: calc(100% - 35px);
}
.te-checkout-column-small {
  width: 100%;
  max-width: 430px;
}
.te-checkout-loader {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 33vh;
}
.te-checkout.desktop {
  padding: 10px 18px;
}
.te-checkout.desktop .te-checkout-animation {
  padding: 150px 0;
}
.te-checkout-heading {
  font-size: 1em;
  font-weight: normal;
  margin: 0.5em auto 1em;
  max-width: 1000px;
  text-transform: capitalize;
}
.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  padding-left: 15px;
  width: 100%;
}
.te-checkout-heading.toggle p {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
}
.te-checkout-outer-wrapper {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}
.te-checkout-button {
  border-radius: 5px;
  border: 0;
  background-color: #222;
  color: white;
  font-size: 0.76em;
  font-weight: 500;
  height: 40px;
  text-transform: uppercase;
}
.te-checkout-button:disabled {
  background: #a3a0a0;
}
.te-checkout-container {
  background-color: white;
  border-radius: 10px;
  box-sizing: border-box;
  margin: 20px auto 20px;
  max-width: 1000px;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  transition: all 0.5s ease;
  width: 100%;
}
.te-checkout-container * {
  box-sizing: border-box;
}
.te-checkout-container.desktop {
  padding: 15px 30px;
}
.te-checkout-container.desktop .toggle-edit {
  top: 15px;
  right: 30px;
}
.te-checkout-container.small {
  margin-left: 0;
  max-width: 460px;
}
.te-checkout-container.hide {
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.te-checkout-container .toggle-edit {
  position: absolute;
  top: 10px;
  right: 15px;
}
.te-checkout-container p {
  font-size: 0.8em;
}
.te-checkout-container.te-similar {
  padding: 15px 0 15px 10px;
  max-width: 1100px;
}
.te-checkout-container.te-similar.desktop {
  padding: 15px 0 15px 20px;
}
.te-checkout-animation {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
}
.te-checkout-animation p {
  font-size: 1em;
}
.te-checkout-similar-products {
  min-height: 560px;
}
.te-checkout-similar-products.te-xxsmall {
  min-height: 440px;
}
.te-checkout-similar-products.te-xsmall {
  min-height: 500px;
}
.te-checkout-similar-products.te-small {
  min-height: 550px;
}
.te-checkout-similar-products.te-medium {
  min-height: 540px;
}
.te-checkout-similar-products-images {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-xxsmall {
  min-height: 270px;
}
.te-checkout-similar-products-images.te-xsmall,
.te-checkout-similar-products-images.te-small {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-medium {
  min-height: 410px;
}
.te-checkout-similar-products-text {
  text-align: center;
  margin: 100px 15px;
}
.te-checkout-similar-products-text span {
  display: block;
  line-height: 1.5em;
  padding-bottom: 1.5em;
}
.te-checkout-similar-products-text.desktop {
  margin: 120px 20px;
}
.te-checkout-similar-products-text.desktop span {
  display: inline-block;
  padding-right: 5px;
}
.te-checkout-stand-alone {
  background-color: #f5f5f5;
  box-sizing: border-box;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: 20px 10px;
}
.te-checkout-stand-alone .te-checkout-heading {
  padding-left: 15px;
  margin: 0 auto;
}
.te-checkout-stand-alone .te-checkout-heading.toggle {
  margin-bottom: 15px;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal {
  color: #303248;
  font-size: 0.8em;
  margin: 0 auto;
  max-width: 305px;
  background: transparent;
  padding: 15px 0;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal a {
  color: black;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-legal {
  background-color: #e7e7e7;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-internal-autofill-selected {
  border: 0;
  -webkit-box-shadow: 0 0 0 50px white inset;
}
.te-dialog-inner .te-checkout {
  height: 100%;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.te-checkout-product-list {
  background-color: #ffffff;
  border-bottom: 1px solid #e7e7e7;
}
.te-checkout-product-list:last-child {
  border: 0;
}
.te-checkout-product-list > .te-button-clear {
  width: 100%;
}
.te-checkout-product-merchant {
  color: #a3a0a0;
  font-size: 0.7em;
  height: auto;
  overflow: hidden;
  position: relative;
  transition: all 0.5s ease-in-out;
}
.te-checkout-product-merchant-row {
  display: flex;
  line-height: 1.3;
  padding: 5px 0;
}
.te-checkout-product-merchant-row:first-child .label {
  white-space: nowrap;
  width: 95px;
}
.te-checkout-product-merchant-row:first-child .value {
  color: #a3a0a0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.te-checkout-product-merchant-row:last-child {
  padding-bottom: 15px;
}
.te-checkout-product-merchant .label {
  color: #4f5058;
}
.te-checkout-product-merchant .uppercase {
  text-transform: uppercase;
}
.te-checkout-product-merchant .value {
  color: #4f5058;
  padding-left: 10px;
  padding-right: 12px;
}
.te-checkout-product-merchant svg {
  fill: currentColor;
  height: 10px;
  position: absolute;
  right: 0;
  transform: rotate(180deg);
  width: 10px;
}
.te-checkout-product-merchant.closed {
  height: 2.5em;
}
.te-checkout-product-merchant.closed svg {
  transform: rotate(0deg);
}
.te-checkout-product-item {
  border-top: 1px solid #e7e7e7;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  height: 180px;
  padding: 10px 0 0;
}
.te-checkout-product-item-wrapper {
  display: flex;
}
.te-checkout-product-item-summary {
  padding-left: 105px;
  display: flex;
  justify-content: space-between;
  flex: 1;
  align-items: center;
}
.te-checkout-product-item-summary-delivery {
  color: #a3a0a0;
}
.te-checkout-product-item .te-button-clear {
  -webkit-tap-highlight-color: transparent;
  display: block;
}
.te-checkout-product-image {
  background-color: transparent;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  height: 130px;
  margin-right: 15px;
  position: relative;
  width: 90px;
}
.te-checkout-product-image:before {
  position: absolute;
  background: #000;
  opacity: 0.05;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  content: '';
}
.te-checkout-product-info {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding-right: 35px;
}
.te-checkout-product-remove {
  position: absolute;
  right: -10px;
  top: 0;
}
.te-checkout-product-remove svg {
  width: 10px;
  font-size: 10px;
  height: 10px;
}
.te-checkout-product-remove button {
  box-sizing: content-box;
}
.te-checkout-product-brand,
.te-checkout-product-name {
  font-weight: 500;
  line-height: 1.3;
  text-transform: uppercase;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.te-checkout-product-brand {
  color: #a3a0a0;
}
.te-checkout-product-name {
  color: #4f5058;
}
.te-checkout-product-price {
  line-height: 1.3em;
}
.te-checkout-product-price-sales {
  padding-top: 5px;
}
.te-checkout-product-price-sales .sales {
  color: #ec877b;
  display: inline-block;
  padding-left: 5px;
}
.te-checkout-product-price-sales .regular {
  display: inline-block;
  text-decoration: line-through;
}
.te-checkout-product-attribute {
  display: flex;
  flex-wrap: wrap;
}
.te-checkout-product-attribute span {
  display: block;
  padding: 0 10px 2px 0;
  max-width: 90px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
}
.te-checkout-product-quantity-select {
  border: 1px solid #e7e7e7;
  font-size: 0.9em;
  height: 30px;
  margin: 5px 0 0 -1px;
  position: relative;
  width: 85px;
}
.te-checkout-product-quantity-select select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  border: 0;
  height: 30px;
  outline: none;
  padding: 5px 15px;
  width: 100%;
}
.te-checkout-product-quantity-select svg {
  fill: currentColor;
  height: 10px;
  pointer-events: none;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
}
.te-checkout-product-quantity-text {
  border: 1px solid #e7e7e7;
  font-size: 0.9em;
  height: 30px;
  margin: 5px 0 0 -1px;
  position: relative;
  width: 85px;
}
.te-checkout-product-quantity-text input {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  border: 0;
  height: 29px;
  outline: none;
  padding: 5px 15px;
  width: 100%;
}
.te-checkout-product-quantity-text input::-webkit-outer-spin-button,
.te-checkout-product-quantity-text input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.te-checkout-product-line-wrapper {
  display: flex;
  align-items: flex-end;
}
.te-checkout-error {
  color: #ec877b;
  font-size: 0.8em;
  padding-left: 10px;
}
.te-checkout-container-empty {
  text-align: center;
}
