.te-checkout {
  background-color: #f5f5f5;
  box-sizing: border-box;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 10px;
  position: relative;
  width: 100%;
}
.te-checkout-columns-layout {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
}
.te-checkout-columns-layout.mobile {
  flex-direction: column;
}
.te-checkout-columns-layout.mobile .te-checkout-column,
.te-checkout-columns-layout.mobile .te-checkout-column-small {
  max-width: 100%;
  padding: 0;
  width: 100%;
}
.te-checkout-columns-layout.mobile .te-checkout-heading.desktop.toggle {
  padding-left: 15px;
  margin: 0;
}
.te-checkout-column {
  padding-left: 35px;
  width: calc(100% - 35px);
}
.te-checkout-column-small {
  width: 100%;
  max-width: 430px;
}
.te-checkout-loader {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 33vh;
}
.te-checkout.desktop {
  padding: 10px 18px;
}
.te-checkout.desktop .te-checkout-animation {
  padding: 150px 0;
}
.te-checkout-heading {
  font-size: 1em;
  font-weight: normal;
  margin: 0.5em auto 1em;
  max-width: 1000px;
  text-transform: capitalize;
}
.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  padding-left: 15px;
  width: 100%;
}
.te-checkout-heading.toggle p {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
}
.te-checkout-outer-wrapper {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}
.te-checkout-button {
  border-radius: 5px;
  border: 0;
  background-color: #222;
  color: white;
  font-size: 0.76em;
  font-weight: 500;
  height: 40px;
  text-transform: uppercase;
}
.te-checkout-button:disabled {
  background: #a3a0a0;
}
.te-checkout-container {
  background-color: white;
  border-radius: 10px;
  box-sizing: border-box;
  margin: 20px auto 20px;
  max-width: 1000px;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  transition: all 0.5s ease;
  width: 100%;
}
.te-checkout-container * {
  box-sizing: border-box;
}
.te-checkout-container.desktop {
  padding: 15px 30px;
}
.te-checkout-container.desktop .toggle-edit {
  top: 15px;
  right: 30px;
}
.te-checkout-container.small {
  margin-left: 0;
  max-width: 460px;
}
.te-checkout-container.hide {
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.te-checkout-container .toggle-edit {
  position: absolute;
  top: 10px;
  right: 15px;
}
.te-checkout-container p {
  font-size: 0.8em;
}
.te-checkout-container.te-similar {
  padding: 15px 0 15px 10px;
  max-width: 1100px;
}
.te-checkout-container.te-similar.desktop {
  padding: 15px 0 15px 20px;
}
.te-checkout-animation {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
}
.te-checkout-animation p {
  font-size: 1em;
}
.te-checkout-similar-products {
  min-height: 560px;
}
.te-checkout-similar-products.te-xxsmall {
  min-height: 440px;
}
.te-checkout-similar-products.te-xsmall {
  min-height: 500px;
}
.te-checkout-similar-products.te-small {
  min-height: 550px;
}
.te-checkout-similar-products.te-medium {
  min-height: 540px;
}
.te-checkout-similar-products-images {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-xxsmall {
  min-height: 270px;
}
.te-checkout-similar-products-images.te-xsmall,
.te-checkout-similar-products-images.te-small {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-medium {
  min-height: 410px;
}
.te-checkout-similar-products-text {
  text-align: center;
  margin: 100px 15px;
}
.te-checkout-similar-products-text span {
  display: block;
  line-height: 1.5em;
  padding-bottom: 1.5em;
}
.te-checkout-similar-products-text.desktop {
  margin: 120px 20px;
}
.te-checkout-similar-products-text.desktop span {
  display: inline-block;
  padding-right: 5px;
}
.te-checkout-stand-alone {
  background-color: #f5f5f5;
  box-sizing: border-box;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: 20px 10px;
}
.te-checkout-stand-alone .te-checkout-heading {
  padding-left: 15px;
  margin: 0 auto;
}
.te-checkout-stand-alone .te-checkout-heading.toggle {
  margin-bottom: 15px;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal {
  color: #303248;
  font-size: 0.8em;
  margin: 0 auto;
  max-width: 305px;
  background: transparent;
  padding: 15px 0;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal a {
  color: black;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-legal {
  background-color: #e7e7e7;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-internal-autofill-selected {
  border: 0;
  -webkit-box-shadow: 0 0 0 50px white inset;
}
.te-dialog-inner .te-checkout {
  height: 100%;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.te-checkout-payment-summary {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.te-checkout-payment-summary-control-row {
  display: flex;
  margin-bottom: 5px;
}
.te-checkout-payment-summary-toggle-link {
  cursor: pointer;
  text-decoration: underline;
}
.te-checkout-payment-summary-total-price {
  font-weight: bold;
}
.te-checkout-cart-summary-rows-container {
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  padding: 10px;
  font-size: 0.8em;
}
.te-checkout-cart-summary-row {
  display: flex;
  justify-content: space-between;
}
.te-checkout-cart-summary-row .label {
  text-transform: capitalize;
}
.te-checkout-cart-summary-separator {
  height: 1px;
  border-top: 1px solid #e7e7e7;
  margin: 10px 0;
}
.te-checkout-stripe-error {
  color: #cd5550;
  text-align: center;
  margin-top: 20px;
}
.te-checkout-stripe-error {
  color: #cd5550;
  text-align: center;
  margin-top: 20px;
}
.te-checkout-payment-view {
  display: flex;
  flex-direction: column;
}
.te-checkout-tooltip {
  left: calc(50% + 10px);
  position: absolute;
  bottom: 35px;
}
.te-checkout-tooltip.desktop {
  right: 10px;
  left: unset;
  bottom: 18px;
}
.te-checkout-payment-view {
  display: flex;
  flex-direction: column;
}
.te-checkout-payment-toggle {
  display: flex;
  font-size: 0.9em;
  justify-content: center;
  padding: 15px 0;
}
.te-checkout-payment-toggle > span {
  padding-right: 10px;
  text-transform: capitalize;
}
.te-checkout-payment-toggle button {
  text-decoration: underline;
}
.te-checkout-payment-toggle button span::first-letter {
  text-transform: capitalize;
}
.te-checkout-payment-total {
  display: flex;
  font-weight: 500;
  justify-content: center;
  margin-bottom: 15px;
}
.te-checkout-container .__react_component_tooltip {
  max-width: 300px !important;
}
.te-checkout-stripe .te-button {
  border-radius: 5px;
  display: block;
  font-size: 0.9em;
  line-height: 2.7em;
  margin: 0 auto;
  max-width: 410px;
  overflow: hidden;
}
.te-checkout-stripe-wrapper {
  background-color: white;
  border-radius: 10px;
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1000px;
  overflow: hidden;
  width: 100%;
}
.te-checkout-stripe-input {
  border-bottom: 1px solid #e7e7e7;
  padding: 15px 10px;
}
.te-checkout-stripe-input.small {
  border-bottom: 0;
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  margin-bottom: -3px;
  width: 50%;
}
.te-checkout-stripe-input:last-of-type {
  border: 0;
}
.te-stripe-legal {
  background-color: white;
  color: #303248;
  font-size: 0.8em;
  line-height: 1.7;
  margin: 0 auto;
  max-width: 305px;
  text-align: center;
}
.te-stripe-legal a {
  color: black;
}
.te-form-input-group .te-stripe-container:first-child {
  margin-right: 20px;
}
div.te-checkout-container.te-klarna {
  padding: 0;
}
div.te-checkout-container.te-klarna .te-klarna-container.after-redirect {
  margin-top: 20px;
  height: 140px;
}
div.te-checkout-container .te-klarna-iframe.after-redirect {
  display: none;
}
div.te-checkout-container .te-klarna-loader {
  display: none;
}
div.te-checkout-container .te-klarna-loader.after-redirect {
  display: block;
}
