.te-checkout {
  background-color: #f5f5f5;
  box-sizing: border-box;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 10px;
  position: relative;
  width: 100%;
}
.te-checkout-columns-layout {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1100px;
  width: 100%;
}
.te-checkout-columns-layout.mobile {
  flex-direction: column;
}
.te-checkout-columns-layout.mobile .te-checkout-column,
.te-checkout-columns-layout.mobile .te-checkout-column-small {
  max-width: 100%;
  padding: 0;
  width: 100%;
}
.te-checkout-columns-layout.mobile .te-checkout-heading.desktop.toggle {
  padding-left: 15px;
  margin: 0;
}
.te-checkout-column {
  padding-left: 35px;
  width: calc(100% - 35px);
}
.te-checkout-column-small {
  width: 100%;
  max-width: 430px;
}
.te-checkout-loader {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 33vh;
}
.te-checkout.desktop {
  padding: 10px 18px;
}
.te-checkout.desktop .te-checkout-animation {
  padding: 150px 0;
}
.te-checkout-heading {
  font-size: 1em;
  font-weight: normal;
  margin: 0.5em auto 1em;
  max-width: 1000px;
  text-transform: capitalize;
}
.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  padding-left: 15px;
  width: 100%;
}
.te-checkout-heading.toggle p {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
}
.te-checkout-outer-wrapper {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}
.te-checkout-button {
  border-radius: 5px;
  border: 0;
  background-color: #222;
  color: white;
  font-size: 0.76em;
  font-weight: 500;
  height: 40px;
  text-transform: uppercase;
}
.te-checkout-button:disabled {
  background: #a3a0a0;
}
.te-checkout-container {
  background-color: white;
  border-radius: 10px;
  box-sizing: border-box;
  margin: 20px auto 20px;
  max-width: 1000px;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  transition: all 0.5s ease;
  width: 100%;
}
.te-checkout-container * {
  box-sizing: border-box;
}
.te-checkout-container.desktop {
  padding: 15px 30px;
}
.te-checkout-container.desktop .toggle-edit {
  top: 15px;
  right: 30px;
}
.te-checkout-container.small {
  margin-left: 0;
  max-width: 460px;
}
.te-checkout-container.hide {
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.te-checkout-container .toggle-edit {
  position: absolute;
  top: 10px;
  right: 15px;
}
.te-checkout-container p {
  font-size: 0.8em;
}
.te-checkout-container.te-similar {
  padding: 15px 0 15px 10px;
  max-width: 1100px;
}
.te-checkout-container.te-similar.desktop {
  padding: 15px 0 15px 20px;
}
.te-checkout-animation {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
}
.te-checkout-animation p {
  font-size: 1em;
}
.te-checkout-similar-products {
  min-height: 560px;
}
.te-checkout-similar-products.te-xxsmall {
  min-height: 440px;
}
.te-checkout-similar-products.te-xsmall {
  min-height: 500px;
}
.te-checkout-similar-products.te-small {
  min-height: 550px;
}
.te-checkout-similar-products.te-medium {
  min-height: 540px;
}
.te-checkout-similar-products-images {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-xxsmall {
  min-height: 270px;
}
.te-checkout-similar-products-images.te-xsmall,
.te-checkout-similar-products-images.te-small {
  min-height: 400px;
}
.te-checkout-similar-products-images.te-medium {
  min-height: 410px;
}
.te-checkout-similar-products-text {
  text-align: center;
  margin: 100px 15px;
}
.te-checkout-similar-products-text span {
  display: block;
  line-height: 1.5em;
  padding-bottom: 1.5em;
}
.te-checkout-similar-products-text.desktop {
  margin: 120px 20px;
}
.te-checkout-similar-products-text.desktop span {
  display: inline-block;
  padding-right: 5px;
}
.te-checkout-stand-alone {
  background-color: #f5f5f5;
  box-sizing: border-box;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: 20px 10px;
}
.te-checkout-stand-alone .te-checkout-heading {
  padding-left: 15px;
  margin: 0 auto;
}
.te-checkout-stand-alone .te-checkout-heading.toggle {
  margin-bottom: 15px;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal {
  color: #303248;
  font-size: 0.8em;
  margin: 0 auto;
  max-width: 305px;
  background: transparent;
  padding: 15px 0;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal a {
  color: black;
}
.te-checkout-stand-alone .te-checkout-container.te-stripe-legal .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-stripe-wrapper {
  background-color: transparent;
}
.te-checkout-stand-alone .te-checkout-legal {
  background-color: #e7e7e7;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-internal-autofill-selected {
  border: 0;
  -webkit-box-shadow: 0 0 0 50px white inset;
}
.te-dialog-inner .te-checkout {
  height: 100%;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.te-checkout-confirmation {
  color: #4f5058;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.te-checkout-confirmation-heading {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 20px;
  text-align: center;
}
.te-checkout-confirmation-heading.desktop {
  font-size: 28px;
  margin-bottom: 50px;
}
.te-checkout-confirmation-animation {
  width: 95px;
  height: 95px;
  margin: 30px auto;
}
.te-checkout-confirmation-animation.desktop {
  width: 120px;
  height: 120px;
  margin: 50px auto;
}
.te-checkout-confirmation-order-number {
  font-size: 14px;
  max-width: 630px;
  margin: 0 auto 50px auto;
}
.te-checkout-confirmation-order-number span {
  font-weight: 500;
}
.te-checkout-confirmation-order-number.desktop {
  margin: 0 auto 80px auto;
  line-height: 20px;
}
.te-checkout-confirmation-address {
  width: 100%;
  max-width: 630px;
  margin-bottom: 70px;
}
.te-checkout-confirmation-address-heading {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 20px;
}
.te-checkout-confirmation-address-heading:first-letter {
  text-transform: uppercase;
}
.te-checkout-confirmation-address-heading.desktop {
  font-size: 20px;
  margin-bottom: 50px;
}
.te-checkout-confirmation-address-columns {
  display: flex;
  flex-direction: column;
}
.te-checkout-confirmation-address-columns.desktop {
  flex-direction: row;
  justify-content: space-between;
}
.te-checkout-confirmation-address-row {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  margin-bottom: 20px;
}
.te-checkout-confirmation-address-row.desktop {
  margin-bottom: 25px;
}
.te-checkout-confirmation-address-row-heading {
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  margin-bottom: 2px;
}
.te-checkout-confirmation-address-row-heading.desktop {
  font-size: 16px;
  line-height: 22px;
}
.te-checkout-confirmation-links {
  margin: 50px auto 70px auto;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
}
.te-checkout-confirmation-links.desktop {
  margin: 60px auto;
  max-width: 480px;
  padding: 0;
}
.te-checkout-confirmation-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.te-checkout-confirmation-anchor {
  color: #4f5058;
}
.te-checkout-confirmation-anchor:hover {
  cursor: pointer;
}
.te-checkout-confirmation-link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  background: #f2f3f7;
  border-radius: 35px;
  margin-bottom: 10px;
}
.te-checkout-confirmation-link-icon.desktop {
  width: 60px;
  height: 60px;
}
.te-checkout-confirmation-link-text {
  position: absolute;
  font-size: 12px;
  top: 48px;
  white-space: normal;
  text-align: center;
}
.te-checkout-confirmation-link-text.desktop {
  font-size: 14px;
  white-space: nowrap;
  top: 69px;
}
.te-checkout-confirmation-bottom-text {
  font-size: 12px;
  text-align: left;
  line-height: 16px;
  margin-bottom: 50px;
}
.te-checkout-confirmation-bottom-text.desktop {
  font-size: 14px;
  max-width: 630px;
  margin: 0 auto 80px auto;
  line-height: 20px;
}
.te-checkout-confirmation-container {
  margin: 0 auto;
  max-width: 700px;
}
.te-checkout-confirmation-container .te-checkout-container,
.te-checkout-confirmation-container .te-checkout-container.desktop {
  padding: 0;
}
.te-checkout-order-processing {
  color: #4f5058;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.te-checkout-order-processing-animation {
  width: 250px;
  height: 250px;
  margin: 150px 0 50px 0;
}
.te-checkout-order-processing-heading {
  font-size: 28px;
  font-weight: 700;
}
.te-checkout-order-processing-text {
  margin: 30px 0 170px 0;
}
.te-dialog-inner .te-checkout {
  height: 100%;
}
