.te-dialog-delivery-info {
  padding: 0;
  font-size: 1.2em;
}
.te-dialog-delivery-info-list {
  list-style-type: none;
  padding: 0;
  display: block;
}
.te-dialog-delivery-info-item {
  display: flex;
  letter-spacing: 0.08em;
  line-height: 1.6;
}
.te-dialog-delivery-info-item-wrapper {
  display: flex;
  font-size: 14px;
  letter-spacing: 0;
  color: #303248;
}
.te-dialog-delivery-info-icon-wrapper {
  margin-right: 8px;
}
.te-dialog-delivery-info-icon {
  width: 17px;
  margin-top: 3px;
}
.te-dialog-delivery-info-label {
  margin-right: 5px;
  color: #303248;
  font-size: 14px;
}
