.te-dialog-dropdown-container {
  width: 100%;
  margin: 0 auto;
}
.te-dialog-dropdown-container.te-tablet,
.te-dialog-dropdown-container.te-desktop {
  width: 50%;
  margin: 30px 0;
}
.te-dialog-dropdown-select {
  border: 1px solid #e7e7e7;
  text-align-last: left;
  background-color: transparent;
  border-radius: 0;
  font-size: 0.8em;
  position: relative;
}
.te-dialog-dropdown-form-select {
  color: #4f5058;
  padding: 13px 10px;
  width: 100%;
  height: 100%;
  letter-spacing: 1px;
  -webkit-appearance: none;
  -moz-appearance: none;
  border: 0;
  background: transparent;
}
.te-dialog-dropdown-form-select:active,
.te-dialog-dropdown-form-select:hover,
.te-dialog-dropdown-form-select:focus {
  outline: 0;
}
.te-dialog-dropdown-arrow_icon {
  position: absolute;
  width: 1.8em;
  right: 0.5em;
  pointer-events: none;
  top: 55%;
  transform: translateY(-50%);
}
.te-product-dropdown-container {
  width: 100%;
  max-width: 260px;
  margin: 0 auto;
}
.te-product-dropdown-container.te-tablet,
.te-product-dropdown-container.te-desktop {
  margin: 30px 0;
}
.te-product-dropdown-select {
  text-align-last: left;
  background-color: transparent;
  border-radius: 0;
  font-size: 0.8em;
  position: relative;
}
.te-product-dropdown-select:hover {
  cursor: pointer;
}
.te-product-dropdown-title {
  position: relative;
  display: flex;
  border: 1px solid #e7e7e7;
  padding: 15px 20px 15px 15px;
  color: #4f5058;
  max-width: 260px;
}
.te-product-dropdown-title:focus {
  outline: none;
}
.te-product-dropdown-arrow_icon {
  position: absolute;
  width: 1.8em;
  right: 5px;
  pointer-events: none;
  top: 50%;
  transform: translateY(-50%) translateX(-50%) rotate(-90deg);
  transition: all 0.3s ease;
}
.te-product-dropdown-arrow_icon.te-rotate {
  transform: translateY(-50%) translateX(-50%) rotate(0);
}
.te-product-dropdown-menu-wrapper {
  outline: none;
  position: absolute;
  background-color: white;
  z-index: 999999;
  width: 100%;
  max-width: 260px;
  border-left: 1px solid #e7e7e7;
  border-right: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
  box-sizing: border-box;
}
.te-product-dropdown-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.te-product-dropdown-menu-item.te-disabled {
  user-select: none;
  pointer-events: none;
  color: #e7e7e7;
}
.te-product-dropdown-menu-item-wrapper {
  padding: 10px 15px;
}
.te-product-dropdown-menu-item-wrapper:hover {
  background-color: #efefef;
  cursor: pointer;
}
.te-product-dropdown-menu-item-inner {
  display: flex;
  justify-content: space-between;
}
.te-product-dropdown-menu-item {
  outline: none;
}
