.te-popup {
  color: #0d0d0d;
  position: fixed;
  top: 66px;
  z-index: 9000;
  padding: 1em;
  border: 1px solid #333;
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1), 0 0 2px rgba(0, 0, 0, 0.3);
  max-width: 100%;
}
.te-popup-title {
  color: #0d0d0d;
  font-family: inherit;
  font-size: 1em;
  font-weight: bold;
  margin: 0 1.5em 1em 0;
  text-transform: uppercase;
}
.te-popup-wrapper {
  z-index: 1;
  min-width: 300px;
}
.te-popup .te-button-cross {
  width: 15px;
  position: absolute;
  right: calc(1em - 10px);
  top: calc(1em - 10px);
  color: #0d0d0d;
}
.te-popup.error {
  color: #cd5550;
}
@media (max-width: 767px) {
  .te-popup {
    right: 85px;
  }
}
@media (max-width: 425px) {
  .te-popup {
    width: 90%;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 768px) {
  .te-popup {
    right: calc(100% - 95% + 48px);
  }
}
@media (min-width: 1334px) {
  .te-popup {
    right: calc((100% - 1200px) / 2 + 10px);
  }
}
